import React from 'react';
import SubPage from '../../components/SubPage.js';
import '../../components/Details.css';
const availableTickets = Math.floor(Math.random() * (25 - 7 + 1)) + 7;

const data = {
    id: 16,
    src: '/images/img16.jpg',
    text: 'Multi-purpose stadium located in Lisbon, Portugal. Hosting games of S.L. Benfica',
    label: 'Lisbon',
    stadium: 'Estádio da Luz',
    tickets: availableTickets,
    lat: 38.752756116204864,
    lng: -9.18459241207078,
};

function Barcelona() {
    return (
        <div>
            <SubPage
                src={data.src}
                text={data.text}
                label={data.label}
                stadium={data.stadium}
                availableTickets={data.tickets}
                lat={data.lat}
                lng={data.lng}
            />
        </div>
    );
}

export default Barcelona;
