import React from 'react';
import SubPage from '../../components/SubPage.js';
import '../../components/Details.css';
const availableTickets = Math.floor(Math.random() * (25 - 7 + 1)) + 7;

const data = {
    id: 9,
    src: '/images/img9.jpg',
    text: 'Has a seating capacity of 53 394 making it the seventh largest football stadium in England, home of Liverpool FC',
    label: 'Liverpool',
    stadium: 'Anfield',
    tickets: availableTickets,
    lat: 53.430842394596105,
    lng: -2.9608456261021967,
};

function Barcelona() {
    return (
        <div>
            <SubPage
                src={data.src}
                text={data.text}
                label={data.label}
                stadium={data.stadium}
                availableTickets={data.tickets}
                lat={data.lat}
                lng={data.lng}
            />
        </div>
    );
}

export default Barcelona;
