import React from 'react';
import SubPage from '../../components/SubPage.js';
import '../../components/Details.css';
const availableTickets = Math.floor(Math.random() * (25 - 7 + 1)) + 7;

const data = {
    id: 10,
    src: '/images/img10.jpg',
    text: 'The Wanda Metropolitano is the new and state of the art Atletico de Madrid stadium - one of the most modern in all Europe',
    label: 'Madrid',
    stadium: 'Wanda Metropolitano',
    tickets: availableTickets,
    lat: 40.43622189015985,
    lng: -3.599324984467931,
};

function Barcelona() {
    return (
        <div>
            <SubPage
                src={data.src}
                text={data.text}
                label={data.label}
                stadium={data.stadium}
                availableTickets={data.tickets}
                lat={data.lat}
                lng={data.lng}
            />
        </div>
    );
}

export default Barcelona;
