import React from 'react';
import SubPage from '../../components/SubPage.js';
import '../../components/Details.css';
const availableTickets = Math.floor(Math.random() * (25 - 7 + 1)) + 7;

const data = {
    id: 8,
    src: '/images/img8.jpg',
    text: 'One of the most famous football stadiums in Europe, famous for its atmosphere. Home of Borussia Dortmund',
    label: 'Dortmund',
    stadium: 'Signal Iduna Park',
    tickets: availableTickets,
    lat: 51.492668938885124,
    lng: 7.451846669579688,
};

function Barcelona() {
    return (
        <div>
            <SubPage
                src={data.src}
                text={data.text}
                label={data.label}
                stadium={data.stadium}
                availableTickets={data.tickets}
                lat={data.lat}
                lng={data.lng}
            />
        </div>
    );
}

export default Barcelona;
