import React from 'react';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/pages/Home.js';
import Services from './components/pages/Services.js';
import Products from './components/pages/Products.js';
import SignUp from './components/pages/SignUp.js';

//stadiums
import Barcelona from './components/pages/Barcelona.js';
import Manchester from './components/pages/Manchester.js';
import Kiev from './components/pages/Kiev.js';
import Paris from './components/pages/Paris.js';
import London from './components/pages/London.js';
import Milan from './components/pages/Milan.js';
import Monachium from './components/pages/Monachium.js';
import Dortmund from './components/pages/Dortmund.js';
import Liverpool from './components/pages/Liverpool.js';
import './App.css';
import Madrid from './components/pages/Madrid.js';
import Marseille from './components/pages/Marseille.js';
import Lisbon from './components/pages/Lisbon.js';

const ErrorPage = () => {
    return <h1 className='errorPage'>This page does not exists</h1>;
};

function App() {
    return (
        <React.StrictMode>
            <Router basename={process.env.PUBLIC_URL}>
                <Navbar />
                <Switch>
                    <Route path='/' exact component={Home} />
                    <Route path='/services' exact component={Services} />
                    <Route path='/products' exact component={Products} />
                    <Route path='/sign-up' exact component={SignUp} />
                    <Route path='/Barcelona' exact component={Barcelona} />
                    <Route path='/Manchester' exact component={Manchester} />
                    <Route path='/Kiev' exact component={Kiev} />
                    <Route path='/Paris' exact component={Paris} />
                    <Route path='/Milan' exact component={Milan} />
                    <Route path='/Monachium' exact component={Monachium} />
                    <Route path='/London' exact component={London} />
                    <Route path='/Dortmund' exact component={Dortmund} />
                    <Route path='/Liverpool' exact component={Liverpool} />
                    <Route path='/Madrid' exact component={Madrid} />
                    <Route path='/Marseille' exact component={Marseille} />
                    <Route path='/Lisbon' exact component={Lisbon} />
                    <Route component={ErrorPage} />
                </Switch>
            </Router>
        </React.StrictMode>
    );
}

export default App;
