import React from 'react';
import SubPage from '../SubPage.js';
import '../../components/Details.css';
const availableTickets = Math.floor(Math.random() * (25 - 7 + 1)) + 7;

const data = {
    id: 15,
    src: '/images/img15.jpg',
    text: 'Stade Vélodrome, was built to serve as a playing venue for the 1938 World Cup',
    label: 'Marseille',
    stadium: 'Stade Vélodrome',
    tickets: availableTickets,
    lat: 43.269842603193815,
    lng: 5.395938260174881,
};

function Barcelona() {
    return (
        <div>
            <SubPage
                src={data.src}
                text={data.text}
                label={data.label}
                stadium={data.stadium}
                availableTickets={data.tickets}
                lat={data.lat}
                lng={data.lng}
            />
        </div>
    );
}

export default Barcelona;
